<template>
    <section>
        <div>
            <loader :is-visible="store.getLoadingStatus"></loader>
            <tg-list :RenderRowActionMethod="renderActions" IdentityColumn="trackId" ModuleName="Timer"
                :listType="[DetailListType]" HeaderText="Flag Track Entry" SubModuleCode="Timer" :SortExp="SortExp"
                :callbackfunction="FetchData" :LegendArray="LegendArray" :SortBy="SortBy" :IsShowAction="false"
                :ListData="store.getTrackEntryFlag" :HeaderData="Headers" :TotalRecords="TotalRecords" :showCheckBox="true"
                :ListDataCallBackFunction="FetchData" :NorecordfoundText="$t('NoRecordfound')" :HeaderButtons="entryButton"
                :widgets="[]" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:PagerButtonClick="pagerMethod"
                v-on:SortdataButtonClick="sortdata" v-on:LeftsearchButtonClick="leftCommonSearch"
                :SearchFields="leftSearchFields">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'comment'">
                        <div @click="commentModalShow(data.row, true)">
                            <em aria-hidden="true" class="fa fa-comment-o  text-primary"></em>
                            <span class="noti-circle noti-blue text-center">{{ data.row.commentsCount }}</span>
                        </div>
                    </template> 
             
              
                    <template v-if="data.column.COLUMN_NAME == 'projectName'">
                            <a class="text-center" @click="updateTrackRecord(data.row)">{{ data.row.projectName }}- {{ data.row.taskName }}</a>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'startTime'">
                       
                            <span class="text-center" v-html="data.row.startTime"></span>-
                            <span class="text-center" v-html="data.row.stopTime"></span>
                        
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'pausedHours' && data.row.pausedHours!=0">
                       <span class="text-center">{{ convertSecondToHHMMSS(data.row.pausedHours) }}</span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'totalHours'">
                       <span class="text-center">{{ convertSecondToHHMMSS(data.row.totalHours) }}</span>
                    </template>
                    
                </template>
            </tg-list>
        </div>
        <Comments v-if="commentShow" v-bind:store="store" v-on:close="commentShow = false" v-bind:flagData="flagData"
            v-bind:trackIds="checkBoxVal" v-on:refreshList="FetchData"/>
            <UpdateTrackTime v-if="updatetrackTimeShow" v-on:close="updatetrackTimeShow=false" v-bind:store="store" v-bind:trackUpdatRecord="trackUpdateRecords" v-on:closeUpdateModal="FetchData"></UpdateTrackTime>
    </section>
</template>

<script>
import {
    defineComponent,
} from '@vue/composition-api'
import Entry from './EntryFlag'
import Comments from "@/components/EntryFlags/FlagUnFlag.vue"
import {convertSecondToHHMMSS} from "@/assets/common/vue-common.js"
import moment from "moment"
import UpdateTrackTime from '@/components/TimeTrack/modals/UpdateTrackTime.vue';
import {
    storeToRefs
} from "pinia";
import {
    useloggedUserInfo
} from "@/stores/UserInfo";
const {
    GetUserInfo
} = storeToRefs(useloggedUserInfo());
export default defineComponent({
    components: {
        Comments,
        UpdateTrackTime
    },
    props: {
        store: {
            type: Object
        },

    },
    created: async function () {
        Entry.bindleftCommonSearchdropdown(this, GetUserInfo.value.UserType);
        //     let url = `projectId=0&datefrom=&dateto=&pageSize=10&pageNum=1&sortBy=&sortExp=`
        //    await this.store.trackEntryFlag(url)
        this.LegendArray = [
            {
                class: 'legends_profile colorboxsquare',
                text: (this.$t('Pending'))
            },
            {
                class: 'lg-approved colorboxsquare',
                text: (this.$t('Approved'))
            },
            {
                class: 'legends_high colorboxsquare',
                text: (this.$t('AddedManually'))
            },
            {
                class: 'legends_medium colorboxsquare',
                text: (this.$t('BulkTimeEntry'))
            },
            {
                class: 'lg-pausedresumed colorboxsquare',
                text: (this.$t('ThirdParty'))
            },
            {
                class: 'legends_Critical colorboxsquare',
                text: (this.$t('FlaggedEntry'))
            },
            {
                class: 'legends_low colorboxsquare',
                text: (this.$t('Timesheet'))
            }
        ]
        this.FetchData()
        

    },
    methods: {
        async FetchData() {
            var vueObj = this;
            var url = `pageSize=${vueObj.pageSize}&datefrom=${vueObj.GetFromDate}&dateto=${vueObj.GetToDate}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&${vueObj.searchCondition}`;
            await this.store.trackEntryFlag(url)
            vueObj.TotalRecords = this.store.getTrackEntryFlag[0].total
            vueObj.trackId = this.store.getTrackEntryFlag[0].trackId
            let checkBox = false
            vueObj.store.getTrackEntryFlag.map((item) => {

                if (item.statusCode.toUpperCase() == "TRACK_ENTRY_APPROVED" || item.statusCode.toUpperCase() == "TRACK_ENTRY_REJECTED" || item.statusCode == "TRACK_ENTRY_DELETE_PENDING" || item.approversCount != 0 || item.projectStatus == "INACT" || item.projectStatus == "DEL" || item.createdByName == GetUserInfo.value.Name) {
                    item.isCheckBoxDisabled = true
                } else {
                    checkBox = true
                    item.isCheckBoxDisabled = false
                }
                item.additionalClass = vueObj.addClass(item.statusCode, item.trackAgent, item.trackBy)
                // console.log("checaddClasskkkk", vueObj.addClass(item.statusCode, item.trackAgent, item.trackBy))
                return item
            })
            if (checkBox == false) {
                let checkBoxNode = document.querySelector(".chkItems")
                checkBoxNode.disabled = true
            }
            //  console.log("check", vueObj.store.getTrackEntryFlag[0])

        },
        commentModalShow(data, show) {
            this.checkBoxVal = data.trackId
            this.flagData = data
            this.commentShow = show

        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "Flag":
                    this.openModal()

                    break;
                case "UnFlag":
                    this.openModal()
                    break;

            }
        },
        openModal() {
            this.checkBoxVal = "";
            var inputElements = document.getElementsByClassName('chkItems');
            for (var i = 0; inputElements[i]; ++i) {
                if (inputElements[i].checked) {
                    if (this.checkBoxVal.length > 0)
                        this.checkBoxVal += ',';
                    this.checkBoxVal += inputElements[i].value;
                }
            }
            this.commentShow = true
        },
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            var vm = this;
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            if(value.searchCondition.includes('DATE')){
                var GetToAndFromDate = value.searchCondition.split("'");
                GetToAndFromDate.forEach(Gdate=>{
                    var getdate = this.$options.filters.formatDate(Gdate);
                    if(getdate != 'Invalid date'){
                        if(vm.GetFromDate == ''){
                            vm.GetFromDate = getdate;
                            console.log(vm.GetFromDate);
                        }else{
                            vm.GetToDate = getdate;
                            console.log(vm.GetToDate);
                        }
                    }
                })
            }     
            // if (value.searchConditionJson.includes('projectId')) {
            //     console.log("project", value.searchConditionJson)
            //     this.projectId = value.searchConditionJson;
            // } else {
            //     this.Userid = value.searchConditionJson;
            // }
            // this.projectId = this.projectId ? this.projectId : 'projectId='
            // this.Userid = this.Userid ? this.Userid : 'userId='
            this.modelinputdate = null;
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        addClass(statusCode, TrackAgent, trackBy) {

            var entryType = statusCode.toUpperCase();
            var entryStatusClass = "";
            var manualEntry = "";
            var trackClass = '';
            switch (entryType) {

                case "TRACK_ENTRY_REJECTED":
                    entryStatusClass = "delpending";

                    break;
                case "TRACK_ENTRY_UPDATE_PENDING":
                    entryStatusClass = "high-profile-bar";

                    break;
                case "TRACK_ENTRY_DELETE_PENDING":
                    entryStatusClass = "high-profile-bar";

                    break;
                case "TRACK_ENTRY_ADD_MANUAL_PENDING":
                    entryStatusClass = "high-profile-bar";

                    break;
                case "TRACK_ENTRY_APPROVED":
                    entryStatusClass = "lgtr-approved";
                    break;
                case "SCREEN_FLAGED":
                    entryStatusClass = "critical-bar";
                    break;
                default:
                    entryStatusClass = "";
                    //  tip = "";
                    break;
            }

            if (TrackAgent) {
                if (TrackAgent.indexOf("Manual") > 0) {
                    manualEntry = "high-bar";

                }
                else if (TrackAgent.toLowerCase() == "bulk") {
                    manualEntry = "medium-bar";

                }
                else if (TrackAgent.toLowerCase() == "timesheet") {
                    manualEntry = "low-bar";

                }
                else if (TrackAgent.toLowerCase() == "third_party") {
                    manualEntry = "lgtr-pausedresumed";

                }
            }
            if (trackBy) {
                switch (trackBy.ToLower()) {
                    case "desktop":
                    case "Linux":
                        trackClass = "trackby_desktop";
                        break;

                    case "web":
                        trackClass = "trackby_web";
                        break;

                    case "web_manual":
                        trackClass = "trackby_web";
                        break;

                    case "third_party":
                        trackClass = "trackby_thirdparty";
                        break;

                    case "android":
                    case "iphone":

                        trackClass = "trackby_mobile";
                        break;

                    //for desktop offline have to change the class by designer
                    case "desktop_offline":
                        trackClass = "trackby_desktop_offline";
                        break;


                    case "bulk":
                        trackClass = "bulkentry_web";
                        //tooltip = Resources.BulkTimeEntry;
                        break;
                    case "timesheet":
                        trackClass = "trackby_timesheet";
                        //tooltip = Resources.BulkTimeEntry;
                        break;

                    case "timesheet_mobile":
                        trackClass = "trackby_tsmobile";
                        break;

                    default:
                        trackClass = "trackby_mobile";
                        break;
                }
            }

            return entryStatusClass + ' ' + trackClass + ' ' + manualEntry

        },
        convertSecondToHHMMSS(val){
            return convertSecondToHHMMSS(val)
        },
        updateTrackRecord(item){
            
            this.trackUpdateRecords = item
            
            this.trackUpdateRecords.PROJECT_ID = item.projectId
            this.trackUpdateRecords.TASK_ID = item.taskId
            this.trackUpdateRecords.TAG_ID = item.tag_name
            this.trackUpdateRecords.TRACK_DESCRIPTION = item.trackDescription
            this.trackUpdateRecords.TRACK_START_TIME = item.startTime
            this.trackUpdateRecords.TRACK_END_TIME =item.stopTime
            this.trackUpdateRecords.TRACK_ID = item.trackId
            this.trackUpdateRecords.DateSet = item.dateStart
            this.updatetrackTimeShow = true
            console.log("fdf",item)
        },

    },
    data() {
        return {
            updatetrackTimeShow:false,
            Headers: Entry.Headers,
            SortBy: '',
            SortExp: "",
            pageSize: 20,
            pageNum: 1,
            TotalRecords: 0,
            TotalPages: 1,
            leftSearchFields: Entry.leftsearchSchema,
            entryButton: Entry.buttons,
            projectId: '',
            userid: '',
            trackId: 0,
            showCommentModal: false,
            commentShow: false,
            DetailListType: 'List',
            flagData: '',
            checkBoxVal: '',
            LegendArray: [],
            dateFrom:'',
            GetFromDate:'',
            GetToDate:'',
            trackerData :{
            projectId: 0,
            trackDescription: "",
            taskId: 0,
            isBillable: 0,
            format: false,
            date: null,
            trackId: 0,
            startTime: moment().format("YYYY-MM-DD HH:mm:ss"),
            stopTime: moment().format("YYYY-MM-DD HH:mm:ss"),
            companyId: null,
            currentDate: moment().format("YYYY-MM-DD HH:mm:ss"),
            trackAgent: "web",
            timerStatus: "start",
            CreatedBy: this.store.getUserInformation.id,
            createdBy: this.store.getUserInformation.id,
            linkedTrackId:null,
            totalHours: 0,
            timeformat: 12,
            TagIds: "",
            isForcedUpdate: null,
            type: "TimeTrack",
            trackType:"TimeTrack",
            BreakDate: null,
            BreakName: null,
            BreakId: '',
            BreakStartTime: 0,
            Shift_ID: 0,
            SelectedDate: "",
            Break_Shift: "",
        }, 
        }
    },
})
</script>
